import axios from 'axios'

// Establecer la URL base dependiendo del entorno (produccin o desarrollo)
const baseURL = process.env.NODE_ENV === 'production'
  ? 'https://tu-api.com' // URL de tu servidor en produccin
  : 'http://localhost:5000'; // URL de tu servidor en desarrollo

// Funcin para GET request
export const getDataAPI = async (url, token) => {
    const res = await axios.get(`${baseURL}/api/${url}`, {
        headers: { Authorization: token }
    })
    return res;
}

// Funcin para POST request
export const postDataAPI = async (url, post, token) => {
    const res = await axios.post(`${baseURL}/api/${url}`, post, {
        headers: { Authorization: token }
    })
    return res;
}

// Funcin para PUT request
export const putDataAPI = async (url, post, token) => {
    const res = await axios.put(`${baseURL}/api/${url}`, post, {
        headers: { Authorization: token }
    })
    return res;
}

// Funcin para PATCH request
export const patchDataAPI = async (url, post, token) => {
    const res = await axios.patch(`${baseURL}/api/${url}`, post, {
        headers: { Authorization: token }
    })
    return res;
}

// Funcin para DELETE request
export const deleteDataAPI = async (url, token) => {
    const res = await axios.delete(`${baseURL}/api/${url}`, {
        headers: { Authorization: token }
    })
    return res;
}
